export const cmsMetaMixin = {
    data: function() {
        return {};
    },

    methods: {},
    created: function() {
        // 
        this.post.client = this.$store.state.client;
    },
    watch: {
        $route: {
            immediate: true,
            deep: true,
            handler() {
                this.init();
            },
        },
        // 
        "$store.state.user_conf.editor_mode": {
            immediate : true,
            handler : function(mode) {
                if (!this.id) {
                    this.post.post_mode = mode || "tinymce";
                }
            },
        }
    },
};
